/*======================
    404 page
=======================*/

.wrap_error404{
    width: 100%;
    
}

.page_404 {
  padding: 40px 0;
  background: #fff;
  font-family: "Arvo", serif;
  height: 100vh;
  display: flex;
    align-items: center;
    justify-content: center;
}

.container_404{
    display: flex;
    align-items: center;
    flex-direction: column;
}

.page_404 img {
  width: 100%;
}

.four_zero_four_bg {
  background-image: url(https://cdn.dribbble.com/users/285475/screenshots/2083086/dribbble_1.gif);
  height: 400px;
  width: 920px;
  background-position: center;
}

.four_zero_four_bg h1 {
  font-size: 80px;
      font-family: inherit;
}

.four_zero_four_bg h3 {
  font-size: 80px;
}

.link_404 {
  color: #fff !important;
  padding: 10px 20px;
  background: #39ac31;
  margin: 20px 0;
  display: inline-block;
}
.contant_box_404 {
  margin-top: -50px;
}
